<template>
  <div class="write-message">
    <h1></h1>
    <form @submit.prevent="createOrder()">
      <div>
        <label for="email">Email</label>
        <input
          type="text"
          name="email"
          placeholder="Email"
          v-validate="'required|email'"
          v-model="form.user.email"
        >
        <div v-if="errors.has('email')" class="form-prompt">{{errors.first('email')}}</div>
      </div>
      <div>
        <label for="name"></label>
        <input
          type="text"
          name="name"
          placeholder=""
          v-validate="'required'"
          v-model="form.user.name"
        >
        <div v-if="errors.has('name')" class="form-prompt"></div>
      </div>
      <div>
        <label for="iPhone"></label>
        <input
          type="text"
          name="ipone"
          id
          placeholder=""
          v-model="form.user.tel"
          v-validate="'required'"
        >
        <div v-if="errors.has('ipone')" class="form-prompt"></div>
      </div>
      <div>
        <label for="iPhone"></label>
        <input
          type="text"
          name="ipone"
          id
          placeholder=""
          v-model="form.user.address"
          v-validate="'required'"
        >
        <div v-if="errors.has('ipone')" class="form-prompt"></div>
      </div>
      <div>
        <label for="message"></label>
        <textarea
          name="message"
          id
          cols="30"
          rows="10"
          placeholder=""
          v-model="form.message"
          v-validate="'required'"
        ></textarea>
        <div v-if="errors.has('message')" class="form-prompt"></div>
      </div>
      <button class="btn"></button>
    </form>
  </div>
</template>
<script>
export default {
  name: 'writeMessage',
  data () {
    return {
      form: {
        user: {},
        message: ''
      }
    }
  },
  methods: {
    async createOrder () {
      this.$store.commit('IS_LOADING', true)
      let api = `${process.env.VUE_APP_LOC}/api/${process.env.VUE_APP_API}/order`
      const isValid = await this.$validator.validate()
      if (!isValid) {
        this.$store.commit('IS_LOADING', false)
        return
      }
      this.axios.post(api, { data: this.form }).then(response => {
        this.$store.commit('IS_LOADING', false)
        this.$emit('timelineStatus', 2)
        this.$store.commit('GET_CART', '')
        this.$router.push(`/order/checkout/${response.data.orderId}`)
      })
      requestAnimationFrame(() => {
        this.$validator.reset()
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.write-message {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  margin-top: 50px;
  div {
    margin-bottom: 30px;
  }
  form {
    width: 50%;
    @include media(xs) {
      width: 80%;
    }
    margin: auto;
    .form-prompt {
      color: color(warning);
    }
  }
  input,
  textarea {
    @include col(20, 3);
  }
}
</style>
